            $scope.entity.RDFromDate = moment($scope.entity.RDResignationDate).format('DD/MMMM/YYYY');
            $scope.entity.RDTodate = moment($scope.entity.RDResignationDate).add(45, 'days').format('DD/MMMM/YYYY');
